.projects {
	margin: 60px 0px;
}

.projects-content {
	padding: 50px 0px;
	box-sizing: border-box;
	margin-bottom: 30px;
	opacity: 0;
	border-radius: 5px;
}

.projects-description {
	display: flex;
	flex-direction: column;
	height: 100%;
	width: 40%;
	padding: 0px 30px 0px 50px;
	box-sizing: border-box;
}

.projects-description h3 {
	font-size: var(--text-size);
	color: white;
	margin: 0px;
}

.projects-description button,
.projects-nav button {
	background: none;
	border: none;
	outline: none;
	cursor: pointer;
}

.projects-description button {
	background: rgba(0, 0, 0, 0.1);
	font-size: var(--text-size);
	margin: 0px;
	margin-top: 10px;
	padding: 10px 20px;
	border-radius: 5px;
	width: fit-content;
}

.projects-image {
	background: url(/website-bg.png);
	background-repeat: no-repeat, repeat;
	background-size: cover;
	width: 850px;
	height: 700px;
	float: right;
	margin-top: -450px;
}

.projects-image img {
	width: 100%;
	box-sizing: border-box;
	padding: 71px 35px;
}

.show {
	opacity: 1;
	transition: opacity 1.5s ease-in;
}

@media only screen and (min-width: 1250px) and (max-width: 1450px) {
	.projects-image {
		width: 700px;
		height: 575px;
		margin-top: -400px;
	}

	.projects-image img {
		padding: 60px 30px;
	}

	.projects-description {
		width: 43%;
	}
}

@media only screen and (min-width: 1120px) and (max-width: 1250px) {
	.projects-image {
		width: 600px;
		height: 500px;
		margin-top: -350px;
	}

	.projects-image img {
		padding: 50px 25px;
	}

	.projects-description {
		width: 40%;
	}
}

@media only screen and (min-width: 850px) and (max-width: 1120px) {
	.projects-content {
		display: flex;
		justify-content: center;
		align-items: center;
		padding: 0px;
		padding-left: 30px;
	}

	.projects-description {
		padding: 0px;
		width: calc(100% - 500px);
	}

	.projects-image {
		float: none;
		width: 500px;
		height: 425px;
		margin: 0px;
	}

	.projects-image img {
		padding: 42px 20px;
	}
}

/* - - - - - - - - - - - - - - - - - - - */

@media only screen and (max-width: 850px) {
	.projects {
		margin: 30px;
	}
	.projects-content {
		padding: 0px;
		padding-top: 30px;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	.projects-description {
		padding: 0px 30px 10px 30px;
	}

	.projects-description,
	.projects-image {
		margin: 0px;
		width: 100%;
	}

	.projects-image {
		width: 100%;
		height: fit-content;
		margin: 0px;
		padding: 0px;
		box-sizing: border-box;
	}

	.projects-image img {
		padding: 8.5% 4%;
		padding-bottom: 0%;
		box-sizing: border-box;
	}
}

@media only screen and (max-width: 600px) {
	.about {
		padding: 20px;
	}

	.projects {
		margin: 30px 0px;
	}

	.projects-content {
		border-radius: 0px;
		padding: 45px 0px 20px 0px;
	}

	.projects-description {
		padding: 0px 20px 10px 20px;
		margin-bottom: 15px;
	}
}
