.no-scroll {
	height: 100%;
	overflow: hidden;
}

.projectDetails {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	margin: auto;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	background: rgba(0, 0, 0, 0.95);
	animation: darken 1s ease;
}

hr {
	border: 2px dotted black;
	margin: 75px 0px;
}

@keyframes darken {
	from {
		background: none;
	}
	to {
		background: rgba(0, 0, 0, 0.95);
	}
}

.projectDetails-content {
	width: 600px;
	font-size: 30px;
	height: 98%;
	overflow: scroll;
	border-radius: 10px 10px 0px 0px;
	box-sizing: border-box;
	padding: 0px;
	position: relative;
	animation: slide-up 1s ease;
}

@keyframes slide-up {
	from {
		margin-bottom: -150vh;
	}
	to {
		margin-bottom: 0px;
	}
}

.projectDetails-content-text {
	text-align: center;
	margin-bottom: 50px;
}

.projectDetails-content-text h1 {
	font-size: 30px;
	margin: -50px auto 50px auto;
	width: 90%;
	color: white;
	text-align: center;
	text-shadow: rgb(105, 105, 105) 1px 0 10px;
}

.projectDetails-content-text h3 {
	font-size: 20px;
	margin: 0px 0px 20px 0px;
}

.projectDetails-content-text p {
	padding: 0px 30px;
	text-align: left;
	box-sizing: border-box;
	font-size: 16px;
}

.projectDetails-content-text img:nth-child(1) {
	width: 100%;
	min-width: 100%;
	max-width: 100%;
	margin: 0px;
	padding: 0px;
	border: none;

	-webkit-box-shadow: none;
	-moz-box-shadow: none;
	box-shadow: none;
}

.projectDetails-content-text img {
	max-width: 95%;
	margin: 50px 0px;
	box-sizing: border-box;

	-webkit-box-shadow: 0 8px 6px -6px black;
	-moz-box-shadow: 0 8px 6px -6px black;
	box-shadow: 0 8px 6px -6px black;
}

.close-btn {
	position: fixed;
	top: 20px;
	right: 20px;

	background: none;
	border: none;
	color: white;
	box-sizing: border-box;
	padding: 20px;
	font-size: 20px;
	font-weight: bold;
	outline: none;
	cursor: pointer;
	animation: show 1s ease;
}

@keyframes darken {
	from {
		opacity: 0;
	}
	to {
		opacity: 1;
	}
}

@media only screen and (max-width: 600px) {
	.projectDetails-content {
		min-width: auto;
		width: 98%;
	}

	.close-btn {
		top: 75px;
		right: 0px;
		animation: show 3s ease;
	}
}
