:root {
	--text-size: 16px;

	/* colors */
	--bg-color: #00040a;
	--text-color: #728094;
	--highlight-color: #fff;
}

html,
body,
#root {
	margin: 0px;
	padding: 0px;
	height: 100%;
	background: var(--bg-color);
}

#root {
	display: flex;
	flex-direction: row;
	justify-content: center;
	align-items: center;
}

.container {
	max-width: 1400px;
	width: 90%;
	font-family: Verdana, Geneva, Tahoma, sans-serif;
	font-size: var(--text-size);
	line-height: calc(var(--text-size) * 2);
	color: var(--text-color);
}

@media only screen and (max-height: 750px) {
	#root {
		height: fit-content;
		align-items: initial;
	}
	.container {
		margin: 30px 0px;
	}
}

@media only screen and (max-width: 850px) {
	#root {
		display: block;
		padding: 10px 0px;
		height: auto;
	}

	.container {
		width: 100%;
	}
}
