.about h1,
.about h3,
.about p {
	margin: 0px;
	padding: 0px;
}

.about h1,
.about h3 {
	font-weight: normal;
	color: var(--highlight-color);
}

.about h1 {
	font-size: 30px;
	margin: 10px 0px;
}

.about h3 {
	font-size: var(--text-size);
}

@media only screen and (max-width: 850px) {
	.about {
		padding: 20px 30px;
	}
}
