.projects-nav {
	margin: 0px;
}

.projectDetails-nav {
	padding: 20px 0px;
}

.projects-nav button {
	font-size: var(--text-size);
	color: var(--text-color);
	margin: 5px;
}

.projects-nav button:hover {
	color: white;
}

.projects-nav button:nth-child(1) {
	margin-left: 0px;
}

@media only screen and (max-width: 850px) {
	.projects-nav {
		margin: auto;
		width: fit-content;
		padding: 0px 10px;
		box-sizing: border-box;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
	}
}

@media only screen and (max-width: 600px) {
	.projects-nav button {
		width: fit-content;
	}

	.projects-nav button:nth-child(1) {
		margin-left: 5px;
	}
}
